	additionalparking: "(i) Circle Hiking Trail parking area on CTH-M near western boundary of Chippewa Moraine National Scientific Reserve. (No parking at CTH-M Trail access farther east.) (ii) Chippewa Moraine National Scientific Reserves David R. Obey Ice Age Interpretive Center off CTH-M. (iii) 260th Ave. (Rattlesnake Hill Rd.) parking area 0.2 mi west of where the Trail heads off on the north side of the road. Parking area in the corner of a field. Overnight parking permitted.",
