	areaservices6: " ",
	areaservices7: " ",
	"pointsofinterest/sidetrail1": "Chippewa Moraine National Scientific Reserve and the David R. Obey Ice Age Interpretive Center: On Trail (13394 CTH-M, New Auburn; 715-967-2800, dnr.wi.gov/topic/parks/name/chipmoraine/naturecenter.html).  The Ice Age Interpretive Center has many hands-on and interactive activities, including activity books, short films and various displays about geologic, cultural and natural history. The Center is generally open year-round Tuesday to Sunday from 8:30 a.m. to 4:00 p.m. (closed Monday) and has maps, drinking water and helpful staff. Hikers should stop here for information and check-in for the propertys two walk-to primitive campsites. Three loop trails start here and share parts of the Ice Age Trail: the Circle Trail (4.5 mi), Dry Lake Trail (1.8 mi) and Mammoth Nature Trail (0.7 mi). These trails feature numerous interpretive signs described in Hiking Field Trip Guide for Glacial Landforms, available at the center.",
