	easternaccess: "CTH-E: From Medford at the intersection of STH-64 and STH-13, take STH-13 north 15.0 mi to Westboro. At CTH-D turn left and go west 8.5 mi. At CTH-E turn left and go south 3.5 mi to the parking area.",
	additionalparking: "(i) Roadside parking along Lake 19 Rd. (FR-108). (ii) White Birch Trail parking area on Mondeaux Ave. (FR-102). The White Birch Trail (Forest Trail 567) leads 1.1 mi south to the Ice Age Trail. (iii) Small parking area on Mondeaux Ave (FR-102) where the Ice Age Trail leaves the road just west of the crossing of the North Branch of the Yellow River.",
	mobileskillscrewprojectsite: "2003, 2012, 2014",
	areaservices1: "Gilman: See Lake Eleven Segment, p. 68. From the Sailor Creek Rd. (FR-571) Trail access go west ~18 mi.",
	areaservices2: "Mondeaux Dam Recreation Area: See Mondeaux Esker Segment, p. 76. From the CTH-E Trail access go north and east ~4 mi.",
	areaservices3: "Medford: See Pine Line Segment and East Lake Segment, p. 80. From the CTH-E Trail access go east and south ~22 mi. Also see Trail Access and Parking directions, above.",
	areaservices4: " ",
	areaservices5: " ",
	areaservices6: " ",
	areaservices7: " ",
	"pointsofinterest/sidetrail1": "Side TrailChippewa Lobe Interpretive Loop 6.1 miles: Includes 4.6 miles of Loop trail + 1.5 miles of Ice Age Trail to close the loop. Access by foot only: (i) From the west, hike the Ice Age Trail 0.5 mi east from Lake 19 Rd. (FR-108) to a kiosk at the start of the Loop (TA23). (ii) From the north, hike the White Birch Trail (Forest Trail 567) south 1.1 mi, cross the Ice Age Trail (TA18) and continue south another 0.5 mi to its intersection with the Chippewa Lobe Interpretive Loop. This is less scenic than the Ice Age Trail access. (iii) From the east, hike the Ice Age Trail 1.6 mi west of where the Ice Age Trail leaves FR-102 after crossing the North Fork of the Yellow River. A steel sign marks the trail junction (TA16).  The Chippewa Lobe Interpretive Loop, also called the Blue Diamond Trail due to its blazes, is a rugged route that circles a remote roadless area called the Ice Age Primitive Area. Six posts mark the miles oriented counter-clockwise, starting from the Ice Age Trails western access to it at the kiosk. During wet weather, the loop may not be passable, as the Yellow River tributaries flow west to east through the entire section, with little structure at critical wet crossings. A primitive campsite (TA24) is located on the loop 0.1 miles south of its western intersection with the Ice Age Trail. A second secluded, primitive campsite (TA17) is located at the southern end of the loop, 2.7 miles south of the Ice Age Trails western intersection and 1.9 miles south of the Ice Age Trails eastern intersection. Situated with pleasant views of the pond and beaver dam, the exceptional site has a log bench, fire pit and small cast iron table made from an old fire grate. A pit toilet is hidden a few yards to the southeast.",
