	areaservices1: "Montello: Restaurant, grocery store, convenience store, lodging, library, medical care. From John Muir Memorial County Park, go north on CTH-F 7.8 mi. At STH-22 (Main St.) turn left and continue north for 0.8 mi. Area info available from the Montello Area Chamber of Commerce (888-318-0362, montellowi.com).",
