	areaservices7: " ",
	"pointsofinterest/sidetrail1": "Observatory Hill State Natural Area: From John Muir Memorial Park, head north on CTH-F for 0.1 mi. Turn right on Gillette Dr. and go east 2.0 mi. Turn left on 13th Rd. and go north 1.0 mi. Turn right on Gillette Ave. and go east 0.6 mi to the parking area (dnr.wi.gov/topic/lands/naturalareas/index.asp?sna=223).  The highest point in Marquette County, Observatory Hill rises 300 feet above the surrounding landscape. On the exposed rocks at the summit are polished rock surfaces with grooves, striations, and chatter marks created by the passage of the glacial ice 12,000 years ago. The hill was a favorite childhood haunt of John Muir.",
