	areaservices1: "Portage: Restaurant, grocery store, convenience store, general shopping, lodging, camping, library and medical care. On Trail. Most services on USH-51/STH-16/Wisconsin St. and on STH-33. Camping, water, restrooms and showers at Veterans Memorial Fields/Columbia County Fairgrounds (608-742-2178, portagewi.gov; available during nonevent weekends and weekdays). From the Trail route on Mullett St. take Griffith St. 3 blocks southeast. It is located between Superior St. and Wauona Trail. For area info, contact the Portage Area Chamber of Commerce (608-742-6242 or 800-474-2525, portagewi.com). Train service at the Portage Amtrak Station (400 W. Oneida St., 800-872-7245, amtrak. com), an unstaffed station consisting of a small waiting room with no station services.",
