	specialdogregulations: "Dogs are not allowed in Parfreys Glen State Natural Area (SNA) except on the IAT. By law, dogs must be leashed when hiking on the IAT in Parfreys Glen SNA and Devils Lake State Park.",
	otheruses: " ",
	otherhikingopportunities: "Trail into Parfreys Glen State Natural Area and blue-blazed spur trail (SA7) to Solumn Ln. parking area.",
	accessibility: " ",
	westernaccess: "CTH-DL: From Baraboo at the intersection of STH-123 and STH-113, take STH-113 south 6.5 mi. At CTH-DL turn left and go east 2.0 mi to the parking area for Parfreys Glen State Natural Area on the north side of the road.",
	easternaccess: "STH-113: From Baraboo at the intersection of STH-123 and STH-113, take STH-113 south 5.0 mi to the parking area on the west side of the road.",
	additionalparking: "(i) Solum Ln. parking area. A blue-blazed spur trail leads to the Ice Age Trail (SA7).",
	mobileskillscrewprojectsite: "2009",
	areaservices1: "Devils Lake State Park: See Devils Lake Segment, p. 194. From the STH-113 Trail access go west ~3 mi.",
	areaservices2: "Baraboo: See Baraboo Segment, p. 190. From the STH-113 Trail access go north ~5 mi. Also see Trail Access and Parking directions, above.",
	areaservices3: " ",
	areaservices4: " ",
	areaservices5: " ",
	areaservices6: " ",
	areaservices7: " ",
	"pointsofinterest/sidetrail1": "Parfreys Glen State natural Area: On Trail (dnr.wi.gov/topic/Lands/naturalareas/index.asp?SNA=1).  Designated in 1952 as Wisconsins first State Natural Area, Parfreys Glen offers a 0.7-mile nature trail that passes through a spectacular deep gorge carved into the sandstone conglomerate of embedded quartzite pebbles and boulders on the south flank of the Baraboo Hills. The uppermost part of the Glen, with its jumble of huge fallen rocks, is nearly 100 feet deep. Hikers will pass moss-covered walls moist from seepage before the trail ends at a small waterfall. The Glens unique microclimate grows northern flora, including white pine, yellow birch, mountain maple and rare cliff plants. An unusual aquatic ecosystem flourishes in the fast, cold, hardwater stream that flows through the gorge and the Glen harbors a diverse insect fauna with a few rare and threatened species. Pets are not allowed in Parfreys Glen SNA except along the Ice Age Trail.",
