	additionalparking: "Parking areas throughout Devils Lake State Park.",
	mobileskillscrewprojectsite: "2002, 2003, 2004, 2008, 2009, 2012",
	areaservices1: "Devils Lake State Park: Concession stand (seasonal), camping. On Trail (608-356-8301, dnr.wi.gov/topic/parks/name/devilslake; reservations: 888-947-2757, reserveamerica.com).",
	areaservices2: "Baraboo: See Baraboo Segment, p. 190. From the STH-113 northern Trail access go north ~5 mi. Also see Trail Access and Parking directions, above.",
	areaservices3: " ",
	areaservices4: " ",
	areaservices5: " ",
	areaservices6: " ",
	areaservices7: " ",
	"pointsofinterest/sidetrail1": "Aldo Leopold Legacy Center: From the parks north entrance head north on STH-123 to Baraboo. At the intersection of STH-123 (Gollmar Blvd.) and STH-33 (Ringling Blvd.) in Baraboo take STH-33 east then north 6.9 mi. At CTH-U, veer left and continue north then west for 2.6 mi. At Schepp Rd. turn right and go north 1.2 mi. At Levee Rd. turn left and go 0.1 mi (E13701 Levee Rd., Baraboo, 608-355-0279, aldoleopold.org).  The Aldo Leopold Foundation operates the Aldo Leopold Legacy Center, which is an educational and interpretive facility near the Leopold Shack. Here Aldo Leopold converted a chicken coop on his farm and wrote part of his conservation classic masterpiece on land ethics, A Sand County Almanac. This is also the very same land where Aldo Leopold died in 1948 fighting a brush fire. The Legacy Center is an excellent place to learn more about this famous American naturalist and see how the Leopold Foundation is carrying out his message of land ethics today. The Aldo Leopold Legacy Center is open to the public with limited hours. The Aldo Leopold Shack and Farm are not open to the public, except with special permission on a guided tour. Visit the website or contact the Aldo Leopold Foundation and Legacy Center for more information.",
