	westernaccess: "STH-113: From Merrimac at the intersection of Baraboo St. and STH-113/78, take STH-113/78 west for 2.5 mi. Continue on STH-113 north for 2.3 mi to Devils Lake State Parks Roznos Meadow parking area on west side of road.",
	easternaccess: "Marsh Rd. Southern Trail Access: From STH-113/78 at Merrimac turn north on Baraboo St. and go 0.3 mi. At Cemetery Rd., which becomes Marsh Rd., turn left and go west then north 0.9 mi to parking area on the northeast side of the road.",
	additionalparking: " ",
	mobileskillscrewprojectsite: "2012",
	areaservices1: "Merrimac: Restaurant, convenience store. From the Marsh Rd. southern Trail access go south and east 0.9 mi on Marsh Rd., which becomes Cemetery Rd. At Baraboo St. turn right and go south 0.3 mi. For area info, contact Merrimac visitor Information (www.tn.merrimac.wi.gov).",
	areaservices2: "Merry Macs Campground: Camping. 0.7 mi south of Roznos Meadow lot on STH-113 (608-493-2367, merrymacscampground.com).",
	areaservices3: "Devils Lake State Park: See Devils Lake Segment, p. 194. From the STH-113 Trail access go north and west ~5 mi.",
	areaservices4: "Baraboo: See Baraboo Segment, p. 190. From the STH-113 Trail access go north ~6 mi.",
	areaservices5: " ",
	areaservices6: " ",
	areaservices7: " ",
	"pointsofinterest/sidetrail1": "Merrimac Ferry: STH-113 across Lake Wisconsin (608-246-3872, dot.wisconsin.gov/travel/water /merrimac).  Listed in the National Register of Historic Places, the Colsac III ferry (commonly referred to as the Merrimac Ferry) is the lone survivor of about 500 ferries that used to operate in the 19th century across Wisconsin. The name Colsac is the phonetic rendering of the two counties the ferry connects, Columbia and Sauk. The ferry crosses the Wisconsin River at a wider part of the river known as Lake Wisconsin, which is a reservoir produced by a dam downstream. At that spot, it is both the Wisconsin River and Lake Wisconsin. The ferry takes seven minutes to cross approximately 0.45 miles. State-operated, the service is free to all, including cars, bikes and pedestrians, and runs 24 hours, 7 days a week April 15 through November 30. The ferry is closed December 1 through April 14. There is a separate lane for pedestrians and bicycles, boarding from the sidewalk on the east (upriver) side of the ferry. Contact the IATA (800-227-0046, iceagetrail.org) for an alternative pedestrian route when the ferry is closed.",
