	accessibility: " ",
	westernaccess: "Table Bluf Rd.: From the Ice Age Trail Alliance headquarters in Cross Plains, take USH-14 west 1.2 mi At CTH-KP turn right and go north 2.4 mi. At Table Bluff Rd. turn left and go west 0.3 mi to the parking area on the south side of the road.",
	easternaccess: " ",
	additionalparking: "(i) Roadside parking at Hickory Hill St. near the gated access road for Hickory Hill Conservation Park. (ii) Roadside parking at Lewis St. Trail access. (iii) Parking area at Village of Cross Plains Municipal Park pool at 2106 Lewis St. (behind the library). (iv) Parking area behind IATA Headquarters (DA26), 2110 Main St.",
	mobileskillscrewprojectsite: " ",
	areaservices1: "Cross Plains: Restaurants, grocery store, convenience store, general shopping, lodging, library, medical care. On Trail. Most services on Main St. (USH-14). Meals at Crossroads Coffeehouse (2020 Main St., 608-798-2080, crossroadscoffeehouse.net) and Coachs Club (1200 Main St., 608-413-0400, coachsclub.com). Area info available from the Cross Plains Chamber of Commerce (608-843-3166, crossplainschamber.net).",
	areaservices2: "Mendota County Park: Camping. From the IATA headquarters go east 7.0 mi on USH-14. Pass under the Beltline Highway and continue east on University Ave. for 1.0 mi. At Branch St. turn left and go north 0.6 mi. At CTH-M/Century Ave. turn right and go east 1.3 mi to the park entrance (5133 CTH-M, Middleton, 608-224-3730, reservedane.com).",
	areaservices3: "Cedar Hills Campground: Camping. From the IATA headquarters go ~8 mi west on USH-14. At STH-78 go 3.0 mi north to the campground entrance (seasonal; 6406 STH-78, Mazomanie, 608-795-2606).",
	areaservices4: "Middleton: See Valley View Segment and Madison Segment, p. 220. From the IATA headquarters on USH-14 go east ~8 mi. Also see Trail Access and Parking directions, above.",
	areaservices5: "Madison: See Valley View Segment and Madison Segment, p. 220. From the IATA headquarters on USH-14 go east ~13 mi.",
	areaservices6: " ",
	areaservices7: " ",
	"pointsofinterest/sidetrail1": "Cross Plains National Scientific Reserve and Future National Park Service Interpretive Area: From the IATA Headquarters in Cross Plains: Take Main St. (USH-14) east 2.6 mi. At Cleveland Rd. turn right and go south 0.9 mi. At Old Sauk Pass Rd. turn right and go west 0.5 mi to entrance gate. Roadside parking. Do not block the gate with vehicles. From west of Madison on the Beltline Highway (USH-12/14) near Middleton: Take Old Sauk Rd. exit and go west 4.0 mi. At Timber Ln. turn right and go north 0.5 mi. At a bend in the road, continue west on Old Sauk Pass Rd. 0.7 mi. Additional parking: Shoveler Sink, a U.S. Fish and Wildlife Service Waterfowl Production Area, on Timber Ln.  This area features a complex of lands owned by Dane County, the Wisconsin Department of Natural Resources and the National Park Service and includes the Cross Plains Unit of Ice Age National Scientific Reserve, one of nine such units. The area represents a world-renowned example of the interface of glaciated and unglaciated terrain. The relationship between moraine and glaciated landscapes on one side of the moraine and unglaciated bedrock landscapes on the other side is strikingly exhibited. Rugged ridges of the moraine formed during the Wisconsin Glaciation meet the eroded Driftless Area to the south and west. Oak savanna, oak forest, wetlands and prairies cover the area.  Unmarked hiking trails penetrate both the DNR land north of Old Sauk Pass and the NPS-owned land south of the road. Dogs are welcome on leash. There are no public restrooms or other visitor facilities available at this time. The National Park Service is in the process of developing a general management plan for the area. This area will be home to a segment of the Ice Age Trail in the future. Details are available at parkplanning.nps.gov/projectHome.cfm?projectID=21764.",
