	additionalparking: "(i) Ashland Ave. at N. Washington St. parking area. A 0.2-mi spur leads to the Trail. (ii) N. River Rd. parking area (2 hour parking) just north of Mineral Point Ave. (iii) W. Milwaukee St. at the Rock River. (iv) S. River St. parking areas. (v) Dawson Ball Field. (vi) Rotary Botanical Gardens. (vii) Palmer Park. (viii) Blackhawk Meadows Park. (ix) N. Wright Rd. roadside parking. (x) Amhurst Rd. roadside parking. A short spur from the cul-de-sac leads to the Trail. (xi) Deerfield Dr. parking area between Home Depot and theaters. A short spur leads to the Trail.",
	mobileskillscrewprojectsite: " ",
	areaservices1: "Janesville: Restaurant, grocery store, convenience store, general shopping, lodging, library, medical care. Area info available from the Janesville Area Convention and Visitors Bureau (seasonal visitor center located on the Trail in Palmer Park; 800-487-2757, janesvillecvb.com), City of Janesville Parks Department (608-755-3025) and City of Janesville Leisure Services (608-755-3030, www.ci.janesville.wi.us/index.aspx?page=100). Recommended services: (i) From the Trail at Washington St. (CTH-E) go east to Bogg Trotters (2006 N. CTH-E, 608-757-1444) for meals. (ii) From the Trail through the downtown area along the river find meals, convenience store, library and medical care. (iii) From the Trail at Mt. Zion Ave. go west 0.25 mi for meals and convenience store. (iv) From the segment endpoint find meals, groceries, convenience store, general shopping and lodging in the Deerfield Drive/USH-14 area.",
