	areaservices4: " ",
	areaservices5: " ",
	areaservices6: " ",
	areaservices7: " ",
	"pointsofinterest/sidetrail1": "Lincoln-Tallman House: Just west of waypoint RO8 at 426 N. Jackson St. (608-756-4509, rchs.us/lincoln-tallman-house).  The Lincoln-Tallman House is Rock Countys most iconic historical structure. Constructed between 1855 and 1857, this six-floor mansion is an excellent example of Italianate-style architecture. From the basement to the cupola, each floor offers visitors a unique glimpse of daily life in 19th century Rock County. Over seventy percent of the furniture is original, making it one of the most complete historic houses in the Midwest. In October of 1859, Abraham Lincoln stayed at the house after a series of speeches in Beloit, forever cementing the reputation of the Lincoln-Tallman House as where Lincoln slept.",
	"pointsofinterest/sidetrail2": "Rotary Botanical Gardens and Lions Beach: Just west of the Trails western intersection with PalmerDrive (608-752-3885, rotarybotanicalgardens.org).  The 20-acre Rotary Botanical Gardens, dedicated to international peace and friendship, showcase 18 different thematic gardens, many with an international theme, along with award-winning roses, unusual plant combinations, special collections of annuals and more. There is an admission fee. Just west of the Gardens is Lions Pond and Beach, with its large swimming area and changing rooms."
