	areaservices2: "KMSF Southern Unit Backpack Shelter 2: Camping. On Trail. Reservations (available only by calling 888-947-2757) are required to use the shelter and only one group per shelter per night is allowed. No potable water is available at the shelter. Hikers should obtain water from the Forest HQ, 0.2 mi north of the shelter.",
