	areaservices6: " ",
	areaservices7: " ",
	"pointsofinterest/sidetrail1": "Old World Wisconsin: From the STH-59 Trail access, go east on STH-59 for 1.6 mi. At STH-67 turn right and go south 1.4 mi (262-594-6301, oldworldwisconsin.wisconsinhistory.org).  Old World Wisconsin is Americas largest outdoor museum of rural life. Live characters recreate daily historical scenes of farm and village life from a century ago. Farmsteads and settlements representing German, Finnish, Polish, Norwegian, African American, Danish and Yankee pioneers dot nearly 600 acres. Open every day during spring, summer and fall. There is an admission fee.",
