	easternaccess: "UW-Waukesha Field Station at Glacial Drumlin State Trail: From I-94 near Delafield, take Exit 285 onto CTH-C (Kettle Moraine Dr.) and go south 2.8 mi. At STH-18 turn right and go west 2.0 mi. At Waterville Rd. turn left and go south 0.5 mi to the UW-Waukesha Field Station. On the east side of the road, look for the gravel drive entrance. Parking is along the drive and at the main parking area near the buildings. No overnight parking allowed.",
