	areaservices7: " ",
	"pointsofinterest/sidetrail1": "UW-Waukesha Field Station: On Trail (262-965-2227, waukesha.uwc.edu and search Field Station).  Modeled after UW-Madisons Arboretum, the Field Station hosts plant communities that can be found throughout the entire state. Currently there is an 8-acre prairie restoration and oak savanna area, pine plantation and hardwood forest in development. Call for information about educational classes open to the public. Also located here is the Wildlife In Need Center (262-965-3090, helpingwildlife. org). The center is focused on the treatment and care of injured, diseased, or orphaned indigenous wildlife and the return of healthy wildlife to the wild. The Center is primarily run by volunteers. ",
