	areaservices7: "Hartland: See Hartland Segment, p. 282. From the STH-83 Trail access go north ~5 mi.",
	"pointsofinterest/sidetrail1": "Cushing Memorial Park and Wisconsin Veterans Memorial Riverwalk: From the intersection of Cushing Park Rd. and Main St., go north on Cushing Park Rd. 0.1 mi to Cushing Memorial Park and the west end of the Wisconsin Veterans Memorial Riverwalk (cityofdelafield.com/parks_and_recreation. html, 262-646-6220).  Cushing Memorial Park honors the Civil War hero brothers Navy Commander William Cushing, Army 1st Lieutenant Howard Cushing and Medal of Honor recipient Army 1st Lieutenant Alonzo Cushing. A 50-foot Barre granite monument stands on part of the original Cushing homestead. The Wisconsin Veterans Memorial Riverwalk links nine separate U.S. war memorials with educational displays about each war. The 0.75-mile walk winds along the Bark River on a boardwalk and gravel path from Cushing Memorial Parks Peace Garden and Spring (west end), a historic Potawatomi meeting site, to St. Johns Pond (east end) on Genesee Street. The Riverwalks main entry plaza and bridge are just north of the Ice Age Trail route at Main Street and Dopkins Street.",
