	westernaccess: "Ahnapee State Trail at Maple Suger Lane.: From Kewaunee at the intersection of STH-42 and STH-29 (Ellis St.), take STH-29 (Ellis St.) west 1.0 mi. At the CTH-C/STH-29 fork, veer right onto CTH-C (Ellis St.) and continue northwest 1.4 mi. At CTH-C/CTH-F intersection turn right and go northwest 4.8 mi. At CTH-A turn left and go west 1.8 mi. At Maple Sugar Ln. turn right. Roadside parking. Do not block road.",
	easternaccess: "Hathaway Dr. Access Road: From Kewaunee at the intersection of STH-42 and STH-29, take STH-42 north 0.6 mi. At Hathaway Dr. turn left and go west then north 0.2 mi to the shared Ice Age Trail/Ahnapee State Trail access. Roadside parking.",
	additionalparking: "(i) Harold Rickelberg Park in Casco Junction at Sunset Rd. and CTH-A. By foot, it is located 1.0 mi northwest of Maple Sugar Ln./CTH-A junction on the rail-trail. (ii) CTH-C western Trail access parking area on the northeast side of CTH-C. (iii) Bruemmer County Park parking area. (iv) CTH-C eastern Trail access parking area on the north side of CTH-C. (v) Dodge St. Park: From central Kewaunee at the intersection of STH-42 and STH-29, take STH-29 (Ellis St.) west one block. At Dodge St. turn right and go north 3 blocks to the Ahnapee State Trail parking area and skateboard park. Walk the trail west 1.2 mi. (vi) Parking area for the Kewaunee Nature Walk on the corner of Hathaway Dr. and STH-42.",
	mobileskillscrewprojectsite: " ",
	areaservices1: "Ahnapee State Trail: On Trail (920-388-0444 or 920-746-9959, dnr.wi.gov/topic/parks/name/ahnapee/).",
	areaservices2: "Kewaunee: Camping, restaurant, grocery store, convenience store, lodging, library, medical care. From the Hathaway Dr. Trail access take Hathaway Dr. and STH-42 south 0.8 mi. Camping at Cedar Valley Campground just south of Clyde Hill Rd. (N5098 Cedar Valley Road; 920-388-4983, cedarvalleycampground.com) and Kewaunee Village RV Park and Campground just west of STH-42 (333 Terraqua Dr.; 920-388-4851, kewauneevillage.com).",
	areaservices3: " ",
	areaservices4: " ",
	areaservices5: " ",
	areaservices6: " ",
	areaservices7: " ",
	"pointsofinterest/sidetrail1": "C.D. Buzz Besadny Anadromous Fish Facility: From the Ice Age Trail at the intersection with CTH-F head west on CTH-F for 0.5 mi then north a short distance on Ransom Moore Ln. (920-388-1025, dnr.wi.gov/topic/fishing/hatcheries/cdbesadny.html).  The Fish Facility is a spawning trout and salmon egg collection and harvesting facility. Following informational and educational displays, visitors can take a self-guided tour and obtain basic information on trout and salmon life history, facility operations and other related subjects. During spawning, visitors can watch trout and salmon swim up the Kewaunee River. Underwater windows offer views of the fish jumping and splashing their way up a fish ladder to large holding ponds. The Fish Facility grounds are open spring, summer and fall during daylight hours. The Facility building is open March 15 to December 15, MF; hours vary. Call ahead to determine what fish are at the facility; fish are not always present.",
